@use "../global/variables.scss" as v;

.main-nav {
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  z-index: 1;
  background-color: v.$color-white;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;

  &--opened {
    display: block;
  }

  &--closed {
    display: none;
  }
}
