@use "../global/variables.scss" as v;

.hero {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.hero-bg {
  padding: 0 15px 164px;
  background-image: url("../img/cat-bg-mobile.jpg");
  background-size: cover;
  background-position: center;
}


.hero__title {
  margin: 0;
  margin-bottom: 5px;
  color: v.$color-white;
}

.hero__slogan {
  margin: 0;
  line-height: 14px;
  text-transform: uppercase;
  color: v.$color-white;
}

.hero__image-wrapper {
  position: relative;
  height: 135px;
  margin-bottom: 3px;
}

.hero__image {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.hero__button {
  margin: 0 20px;
}
