@use "../global/variables.scss" as v;

.catalog-promo {
  background-image: url("../img/promo-bg.jpg");
  background-size: cover;
  background-position: center;
}

.catalog-promo__text {
  position: relative;
  margin: 0 auto;
  width: 161px;
  padding: 187px 0 43px;
  text-align: center;
  font-family: v.$second-font;
  font-size: 16px;
  line-height: 20px;
  color: v.$color-white;

  &::before {
    position: absolute;
    top: 58px;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 78px;
    height: 81px;
    content: "";
    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}
